
.detail-view header{overflow:hidden;font-size:0.14rem;color:#444;max-width:720px;padding:0.15rem;margin:0.15rem auto 0
}
.detail-view header>.btn-return{float:left;padding:0.05rem 0.15rem;background:#ccc;color:#fff;border-radius:4px;letter-spacing:2px
}
.detail-view header>.email{float:right
}
.detail-view header>.email a{color:#444;text-decoration:underline
}
.detail-view article{max-width:720px;padding:0.15rem;margin:auto;letter-spacing:2px;line-height:1.5;font-size:0.15rem
}
.detail-view article em{font-style:normal;font-weight:600;text-decoration:underline
}
.detail-view article p{margin-top:0.15rem
}
.detail-view article h1{font-size:0.24rem;margin-bottom:0.15rem
}
.detail-view article h1+p{font-size:0.12rem;font-weight:100;color:#555
}
.detail-view article h2{margin:0.3rem 0 0.05rem 0
}
.detail-view article h3{margin:0.15rem 0 0.05rem 0
}
.detail-view article pre{padding:0.1rem;border:1px dashed #ccc
}



