
.index-view{height:100%;position:relative
}
.index-view>.foreground{height:100%;position:relative;width:100%;overflow:hidden;position:relative;z-index:1986
}
.index-view .avator{float:left;height:.3rem;width:.3rem;display:block;float:left;margin-left:.3rem
}
.index-view .search{float:right;margin-right:0.15rem;height:.3rem
}
.index-view .search input{background:rgba(204,204,204,0.38);width:2rem;height:.24rem;line-height:.3rem;margin:.03rem 0.15rem 0 0;display:block;border-radius:6px;padding:0 0.1rem;font-size:0.12rem;float:left
}
.index-view .search .btn{float:left;display:block;cursor:pointer
}
.index-view .search .btn:hover{color:#ccc
}



